//  Bill  vm   
let Bill = (vm) => {
	return {
		save: (params) => {
			return vm.$u.post('bill/save', params);
		},
		
		index: (params) => {
			return vm.$u.post('bill/index', params);
		},
		
		detail: (id) => {
			return vm.$u.get('bill/detail/' + id + '?include=account,category');
		},
		
		destroy: (id) => {
			return vm.$u.delete('bill/' + id);
		}
	}
}

//   
export default Bill
