//  Statistics  vm   
let Statistics = (vm) => {
	return {
		amounts: (params) => {
			return vm.$u.post('statistic/amounts', params);
		},
		
		trends: (params) => {
			return vm.$u.post('statistic/trends', params);
		},
		
		getCategoryCharts: (params) => {
			return vm.$u.post('statistic/categoryCharts', params);
		}
	}
}

//   
export default Statistics
